  src: url("../fonts/robotoslab-regular.woff2") format("woff2"), url("../fonts/robotoslab-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  background-color: #ffffff;
  color: #000000; }

img {
  max-width: 100%;
  object-fit: cover;
  display: block; }

input,
textarea,
select,
button {
  font: inherit;
  line-height: inherit; }

a {
  color: inherit;
  cursor: pointer; }

input[type="checkbox"] ~ label {
  cursor: pointer; }

a:not([href]) {
  cursor: auto; }

button {
  cursor: pointer; }

.visually-hidden,
input[type="checkbox"] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1280px;
  min-width: 320px;
  padding: 0 7.8%; }
  @media (min-width: 768px) {
    .container {
      padding: 0 3.9%; } }
  @media (min-width: 1024px) {
    .container {
      padding: 0 2.4%;
      margin: 0 auto; } }

.price {
  padding-top: 55px; }

.price__layout {
  padding-top: 54px; }

.price__heading {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 38px;
  line-height: 1.16;
  text-align: center;
  letter-spacing: -0.5px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 31px; }

.offers {
  position: relative;
  background-color: #3D3BFF;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  color: #ffffff;
  padding-top: 90px;
  padding-bottom: 40px; }

.offers__full {
