.offers__first {
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: -0.5px;
  margin-top: 0;
  margin-bottom: 0; }

.offers__first-price {
  font-family: "Roboto Slab", "Georgia", serif;
